import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides, MenuController, LoadingController, Loading, Platform, normalizeURL } from 'ionic-angular';
import { WellnessServiceProvider } from '../../providers/wellness-service/wellness-service';
import { ToolsProvider } from '../../providers/tools/tools';
import { NotificationProvider } from '../../providers/notification/notification';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
/**
 * Generated class for the WellnessPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wellness',
  templateUrl: 'wellness.html',
})
export class WellnessPage {
  @ViewChild(Slides) slides: Slides;
  loading: Loading;
  wellnessList: any = [];
  activeSlider: any;

  constructor(private transfer: FileTransfer, private file: File, public navCtrl: NavController,
    public navParams: NavParams, public wellnessProvider: WellnessServiceProvider, public toolsProvider: ToolsProvider,
    public menuCtrl: MenuController, public loadingCtrl: LoadingController,
    private notificationProvider: NotificationProvider, private platForm: Platform) {
    menuCtrl.enable(true);

    if (this.wellnessProvider.objWellness && this.wellnessProvider.objWellness.activeSlider) {
      this.activeSlider = this.wellnessProvider.objWellness.activeSlider;
    }
    //get wellness list
    this.wellnessList = this.wellnessProvider.objWellness.data;

  }

  ionViewWillLeave() {
    //save active slider index
    this.wellnessProvider.objWellness.activeSlider = this.slides.getActiveIndex();
  }

  ionViewDidLoad() {
    if (this.activeSlider) {
      setTimeout(() => {
        this.slides.slideTo(this.activeSlider, 0);
      }, 200);
    }
  }

  //moves to next slide
  slideNext() {
    this.slides.slideNext();
  }

  //moves to previous slide
  slidePrev() {
    this.slides.slidePrev();
  }
  download(itemId, url, fname, i) {
    this.file.checkFile(this.file.cacheDirectory, fname).then(() => {

      this.loading.dismiss();
      let imgUrl = this.file.cacheDirectory + fname;
      if (this.platForm.is('ios')) {
        imgUrl = normalizeURL(this.file.cacheDirectory + fname);
      }
      this.navCtrl.push("WellnessDetailsPage", { itemId: itemId, img: imgUrl }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });

    }, (error) => {

      const fileTransfer: FileTransferObject = this.transfer.create();
      fileTransfer.download(url, this.file.cacheDirectory + fname).then((entry) => {
        this.loading.dismiss();
        let imgUrl = entry.toURL();
        if (this.platForm.is('ios')) {
          imgUrl = normalizeURL(entry.toURL());
        }
        this.navCtrl.push("WellnessDetailsPage", { itemId: itemId, img: imgUrl }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });

      }, (error) => {
        this.loading.dismiss();
        this.navCtrl.push("WellnessDetailsPage", { itemId: itemId, img: "" }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
      });

    });
  }
  fnGotoDetailsPage(itemId) {
    this.showLoading();
    let itemData = this.wellnessProvider.objWellness.data; // get all item list
    itemData = itemData.find(x => x.id == itemId); // get item from item list with given item id
    for (let item of itemData.availableItems) {
      let itemArray: any;
      itemArray = item;
      let imagePath = this.toolsProvider.getImagePath(item.foto.id); // image path using image id
      this.download(itemId, imagePath, item.foto.id + ".jpg", 0);
      break;
    }
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  // slide changed
  slideChanged() {
    this.wellnessProvider.objWellness.activeSlider = this.slides.getActiveIndex();
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
}



